.panel {
  border-radius: 10px;
  margin-bottom: 10px;
  transition: all 300ms linear;
}

.panel-header {
  background-color: #f6f6f6;
  padding: 10px;
  cursor: pointer;
  transition: all 200ms linear;
}

.panel-content {
  display: none;
  padding-bottom: 20px;
  transition: all 300ms linear !important;
}
